.wp-block-wdm-plugins-team-members-group {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1rem;
}

.cols-2 {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.cols-3 {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.cols-4 {
  grid-template-columns: repeat(4, minmax(0, 1fr));
}

/* Editor Styles */
.wp-block-wdm-plugins-team-members-group[data-type="wdm-plugins/team-members-group"] {
  display: inherit;
}

.wp-block-wdm-plugins-team-members-group[data-type="wdm-plugins/team-members-group"]
  .block-editor-block-list__layout {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1rem;
}

.wp-block-wdm-plugins-team-members-group[data-type="wdm-plugins/team-members-group"].cols-2
  .block-editor-block-list__layout {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.wp-block-wdm-plugins-team-members-group[data-type="wdm-plugins/team-members-group"].cols-3
  .block-editor-block-list__layout {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.wp-block-wdm-plugins-team-members-group[data-type="wdm-plugins/team-members-group"].cols-4
  .block-editor-block-list__layout {
  grid-template-columns: repeat(4, minmax(0, 1fr));
}
